/**
  |============================
  | About
  |============================
*/

.about__container {
  padding-top: 30px;
  padding-bottom: 31px;
  padding-left: 52px;
  padding-right: 48px;
  @media screen and (min-width: 768px) {
    padding-top: 40px;
    padding-bottom: 31px;
    padding-left: 55px;
    padding-right: 47px;
  }
  @media screen and (min-width: 1360px) {
    padding-bottom: 19px;
    padding-left: 66px;
    padding-right: 66px;
  }
}
.about__list {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    gap: 53px;
  }
  @media screen and (min-width: 1360px) {
    gap: 71px;
  }
}
.about__item {
  @media screen and (min-width: 768px) {
    width: 185px;
  }
  @media screen and (min-width: 1360px) {
    width: 362px;
  }
}
.about__shell {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 64px;
  height: 64px;
  margin-bottom: 11px;
  border-radius: 50%;
  background-color: var(--accent-bcg);
  &:last-child {
    margin-bottom: 0;
  }
}
.about__title {
  font-weight: 400;
  font-size: 18px;
  line-height: calc(30 / 18);
  text-align: center;
  color: var(--content-txt);
  margin-bottom: 15px;
  &:last-child {
    margin-bottom: 0;
  }
}
.about__txt {
  font-weight: 400;
  font-size: 16px;
  line-height: calc(26 / 16);
  color: var(--content-txt);
  margin-bottom: 15px;
  &:last-child {
    margin-bottom: 31px;
  }
}

.about__aside--right {
  padding: 30px;
  background-color: var(--accent-bcg);
  @media screen and (min-width: 768px) {
    width: 384px;
  }
  @media screen and (min-width: 1360px) {
    width: 680px;
    padding-top: 84px;
    padding-bottom: 84px;
  }
}
.about__aside--left {
  @media screen and (min-width: 768px) {
    width: 384px;
  }
  @media screen and (min-width: 1360px) {
    width: 680px;
  }
}

.about__subject {
  font-weight: 400;
  font-size: 18px;
  line-height: calc(30 / 18);
  color: var(--prymary-txt);
  margin-bottom: 5px;
  @media screen and (min-width: 768px) {
    font-size: 20px;
    line-height: calc(33 / 20);
  }
}
.about__caption {
  font-weight: 300;
  font-size: 38px;
  line-height: calc(48 / 38);
  color: var(--prymary-txt);
  margin-bottom: 25px;
}
.about__desc {
  font-weight: 400;
  font-size: 16px;
  line-height: calc(26 / 16);
  color: var(--prymary-txt);
  margin-bottom: 14px;
  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
    font-size: 18px;
    line-height: calc(28 / 18);
  }
  @media screen and (min-width: 1360px) {
    margin-bottom: 17px;
    width: 606px;
  }
}
.about_btn {
  display: block;
  align-items: center;
  text-align: center;
  width: 157px;
  height: 47px;
  font-weight: 400;
  font-size: 16px;
  line-height: calc(26 / 16);
  color: var(--prymary-txt);
  cursor: pointer;
  border: 1px solid var(--primary-bgc);
  border-radius: 5px;
  background: var(--accent-bcg);
}
.about__box {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}
